import { Button, Typography } from '@mui/material'
import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import enrollController from '../../../Controllers/Enroll/enroll.controller'

function EnrollSubmit() {
    const [enrollDetails, setEnrollDetails] = useState(enrollController.getDetailsSession())

    const navigate = useNavigate()

    return (
        <div style={{ display: 'flex', flexDirection: 'column', gap: '16px' }}>
            <Typography variant="h5" component="h3">
                
            </Typography>
            <Typography>   : {enrollDetails.username}</Typography>
            {enrollDetails.userSurname ? (
                <Typography>  : {enrollDetails.userSurname} </Typography>
            ) : null}
            <Typography>  : {enrollDetails.phoneNumber}</Typography>
            <Typography>
                  : {new Date(enrollDetails.meetingDate).toLocaleDateString()}
            </Typography>
            <Typography>
                  :{' '}
                {new Date(enrollDetails.meetingDate).toLocaleTimeString().slice(0, -3)}
            </Typography>
            <Typography>
                <span>  :</span>
                {enrollDetails.servicesList.map((item) => (
                    <div>{item.name}</div>
                ))}
            </Typography>
            <Typography>
                  {' '}
                {enrollDetails.servicesList.reduce(
                    (sum, obj) =>
                        sum +
                        Object.values(obj).reduce((s, v) => {
                            if (typeof v !== 'string') {
                                return s + v
                            }
                            return s
                        }, 0),
                    0
                )}{' '}
                
            </Typography>
            <Button
                variant="contained"
                onClick={async () => {
                    const res = await enrollController.saveServicesListDB(enrollDetails)
                    navigate('/confirmation/enroll')
                }}
            >
                
            </Button>
            <Button
                variant="contained"
                onClick={() => {
                    navigate('/enroll')
                }}
            >
                  
            </Button>
            <Button
                variant="contained"
                onClick={() => {
                    navigate('/enroll/details')
                }}
            >
                  . 
            </Button>
        </div>
    )
}

export default EnrollSubmit
